.modal {
	height: 100vh;
	width: 100vw;
	background-color: rgba(0, 0, 0, 0.4);
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	opacity: 0;
	pointer-events: none;
	transition: 0.5s;
}

.modal.active {
	opacity: 1;
	pointer-events: all;
}

.modalContent {
	padding: 30px;
	border-radius: 10px;
	background-color: white;
	transform: scale(0.5);
	transition: 0.4s all;
	width: 50vw;
}

.modalContent.active {
	transform: scale(1);
}
